    gap: 60px;
  }
}
.hero-section__content {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.hero-section__title {
  position: relative;
  font-family: var(--second-family);
  color: #f2994a;
  color: var(--color-accent);
  font-weight: 700;
  font-size: 60px;
  line-height: 96%;
  letter-spacing: -0.03em;
  margin: 0;
}
.hero-section__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
}
.hero-section__bottom::before {
  content: "";
  border-bottom: 1px solid rgba(0, 0, 0, 0.09);
  display: block;
  height: 1px;
  width: 100%;
  margin-bottom: 14px;
}
.hero-section__bottom-text {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: var(--secondary);
}
.hero-section__button {
  margin-left: 0;
  width: -moz-fit-content;
  width: fit-content;
  text-wrap: nowrap;
}
.hero-section__img {
  width: 70%;
  -o-object-fit: contain;
     object-fit: contain;
}
@media screen and (min-width: 768px) {
  .hero-section__img {
    width: 50%;
  }
}

.life-section__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
.life-section__item:nth-child(odd) {
  background-color: var(--secondary-light);
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  padding: 16px 24px;
  background-color: #fff5ec;
  background-color: var(--primary);
  transform: translateX(100%);
  transition: transform 0.3s;
}
.mobile-menu.is-open {
  transform: translateX(0);
}
.mobile-menu__btn-close {
  position: absolute;
  top: 16px;
  right: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  fill: transparent;
  stroke: var(--secondary);
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.mobile-menu__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-top: 40px;
}
.mobile-menu__link {
  color: var(--color-primary);
  font-size: 16px;
  line-height: 1.5;
  text-decoration: none;
  font-weight: 600;
}
.mobile-menu__link:hover {
  color: var(--secondary);
}

.section {
  padding: 51px 0;
}
.section__title {
  color: #38404c;
  color: var(--color-text-primary);
  font-weight: 700;
  font-size: 40px;
  line-height: 120%;
}
.section__title--centered {
  text-align: center;
}
.section__subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: 163%;
  text-align: center;
  opacity: 0.8;
}

.thank-you {
  text-align: center;
  padding-top: 124px;
}
.thank-you__button {
  width: 20%;
  margin: 0px auto 40px;
}`, "",{"version":3,"sources":["webpack://./src/scss/utils/_fonts.scss","webpack://./src/scss/styles.scss","webpack://./src/scss/utils/_variable.scss","webpack://./src/scss/utils/_utils.scss","webpack://./src/scss/_base.scss","webpack://./src/scss/components/_application-card.scss","webpack://./src/scss/components/_buttons.scss","webpack://./src/scss/components/_career-card.scss","webpack://./src/scss/components/_edu-card.scss","webpack://./src/scss/components/_links.scss","webpack://./src/scss/components/_input.scss","webpack://./src/scss/components/_life-card.scss","webpack://./src/scss/components/_logo.scss","webpack://./src/scss/components/_policy.scss","webpack://./src/scss/components/_question-card.scss","webpack://./src/scss/layouts/_application-section.scss","webpack://./src/scss/layouts/_subscribe-section.scss","webpack://./src/scss/layouts/_container.scss","webpack://./src/scss/layouts/_cookie-banner.scss","webpack://./src/scss/layouts/_costs-section.scss","webpack://./src/scss/layouts/_education-section.scss","webpack://./src/scss/layouts/_career-section.scss","webpack://./src/scss/layouts/_faq-section.scss","webpack://./src/scss/layouts/_footer.scss","webpack://./src/scss/layouts/_header.scss","webpack://./src/scss/layouts/_hero-section.scss","webpack://./src/scss/layouts/_life-section.scss","webpack://./src/scss/layouts/_mobile-menu.scss","webpack://./src/scss/layouts/_section.scss","webpack://./src/scss/layouts/_thank-you.scss"],"names":[],"mappings":"AAAA,4BAAA;AACA;EACE,kBAAA,EAAA,sGAAA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4DAAA,EAAA,4DAAA;ACCF;ADCA,wBAAA;AACA;EACE,kBAAA,EAAA,sGAAA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4DAAA,EAAA,4DAAA;ACCF;ADCA,wBAAA;AACA;EACE,kBAAA,EAAA,sGAAA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4DAAA,EAAA,4DAAA;ACCF;ADCA,8BAAA;AACA;EACE,kBAAA,EAAA,sGAAA;EACA,wBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4DAAA,EAAA,4DAAA;ACCF;ADCA,0BAAA;AACA;EACE,kBAAA,EAAA,sGAAA;EACA,wBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4DAAA,EAAA,4DAAA;ACCF;ADCA,0BAAA;AACA;EACE